.searchSection {
  padding: 2em;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 8em;
  width: auto;
}

::placeholder {
  font-weight: bolder;
  font-size: 11px;
}

#searchBox {
  padding: 1em;
  background-color: #fff;
  display: flex;
  align-items: center;
  border-radius: 10px;
}

#searchQuery {
  height: 2em;
  width: 25vw;
  font-weight: bolder;
  border: none;
  background-color: white;
}

#SearchIcon {
  width: 2.4em;
  cursor: pointer;
  background-color: white;
}

.searchIcon {
  background-color: white;
}

span {
  background-color: black;
}
